.h {
  animation:  1s ease 0s normal forwards 1 d;
  
}
.m{
  opacity: 0;
  animation: 1s ease-in 0s normal forwards 1 t;
  animation-delay: 1s;
}

@keyframes d {
  0%{
    opacity: 0;
  }
  50%{
   
    transform: translateX(-600px);
  }
  100% {
    opacity: 1;
    transform: translate(-600px,-400px);
  }
}
@keyframes t {
  0%{
    opacity: 0;
  }
  50%{
   
    transform:   rotate(90deg)
  }
  100% {
    opacity: 1;
    transform: translate(-600px,-400px)
  }
}