.header{
    opacity:0;

  /* -webkit-animation: fadein 5s linear 1 normal forwards; */
  animation: 1s ease-in-out 0s  forwards 1 f;

}

.header2{
    opacity:0;
    animation: 3s ease-in-out 0s  forwards 1 fade;
}
.header3{
    opacity:0;
    animation: 6.5s ease-in-out 0s  forwards 1 fade;
}
.st{
    opacity:0;
    animation: 9s ease-in-out 0s  forwards 1 fade;
} 
.nd{
    opacity:0;
    animation: 10s ease-in-out 0s  forwards 1 fade;
}
.rd{
    opacity:0;
    animation: 11s ease-in-out 0s  forwards 1 fade;
}
.sub{
    opacity:0;
    animation: 12s ease-in-out 0s  forwards 1 btn;
}
@keyframes f{
    0% { opacity:0; }
    66% { opacity:0; }
    100% { opacity:1;transform:  translateY(-200px)}
}
@keyframes fade{
    0% { opacity:0; }
    66% { opacity:0; }
    100% { opacity:1;transform:  translateY(-200px)}
}
@keyframes btn{
    0% { opacity:0; }
    66% { opacity:0; }
    100% { opacity:1;transform:  translateY(-200px)}
}
